@import url(https://fonts.googleapis.com/css?family=Permanent+Marker|Rock+Salt);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App{font-family:sans-serif;text-align:center}.player{display:flex;justify-content:space-between;align-items:center;background-color:#6e6e6e}.player .controls{flex-grow:1;display:flex;flex-direction:row;justify-content:center;align-items:center}.player .player__button{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;background-color:transparent;border:none}.player .player__button:focus{outline:none}.player .player__button:hover{cursor:pointer}.player .player__button:hover svg{color:#aaa}.player .player__button svg{font-size:2.8em;color:white;margin-top:5px;margin-bottom:7px}.player .bar{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;width:100%;display:flex;align-items:center}.player .bar .bar__time{color:white;font-size:12px;margin-right:5px}.player .bar .bar__progress{flex:1 1;border-radius:5px;margin:0 4px;height:1px;display:flex;align-items:center;cursor:pointer}.player .bar .bar__progress .bar__progress__knob{position:relative;height:10px;width:10px;border:1.5px solid white;border-radius:50%;background-color:#fff}

